import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import EventListener from 'react-event-listener';
import { AppState } from '../../reducers/app';

interface SailingScrollDownProps {
  text: string;
}

const SailingScrollDown: React.FC<SailingScrollDownProps & AppState> = ({
  text,
  scrollTop,
  windows
}) => {
  const handleScroll = () => {
    const { width, vw, vh } = windows;
    const isSmartPhone = width < 768;
    let headerHeight = 76;
    if (isSmartPhone) {
      headerHeight = 56;
    }

    const fixed = scrollTop >= windows.height - 12 * vh - 169 - 76;

    if (scrollTop >= windows.height - 8 * vh - 173 - headerHeight) {
      setIsFixed(true);
    } else {
      setIsFixed(false);
    }
  };

  const [isFixed, setIsFixed] = useState(false);

  return (
    <div className={`sailingScrollDown ${isFixed && 'fixed feature'}`}>
      <EventListener target="window" onScroll={handleScroll} />
      <span className="sailingScrollDown__txt">{text}</span>
      <span className="sailingScrollDown__arrow"></span>
    </div>
  );
};

export default SailingScrollDown;
